$grid-breakpoints: (
  xs: 0,
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px,
  xxl: 1400px
);

:root {
  --primary: #16abf8;
}

@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;0,500;0,600;0,700;1,400;1,500;1,600;1,700&display=swap);


body {
    margin: 0;
    font-family: "Poppins",sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #f4f4f4;
}

button {
  font-family: "Poppins",sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  cursor: pointer;
}

.container {
  max-width: 1000px;

  &.center {
    margin-left: auto;
    margin-right: auto;
  }
}

.btn-primary {
  color: #fff;
  background-color: var(--primary);
  border-color: var(--primary);
}

span[class^="icon-"] {
  width: 24px;
  height: 24px;
  background-repeat: no-repeat;
  background-size: auto auto;
  display: inline-block;
  vertical-align: middle;
}

.btn.btn-light {
  background-color: unset;
}

.btn.btn-simple {
  border-radius: unset;
  border: 0;
  padding: unset;   
  line-height: 27px;
}

.btn:has(span[class^="icon-"]) {
  display: flex;
  gap: 6px;
}

.todo-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: 43px 0 55px
}

.todo-header h1{
  font-size: 36px;
  font-weight: 700;
}

.dashboard-content {
  display: grid;
  grid-template-columns: auto auto auto auto;
  grid-gap: 26px 20px;
}

/* icons */

.icon-plus {
  background-image: url(./svg/icon-plus.svg);
}

.icon-delete {
  background-image: url(./svg/icon-delete.svg);
}

