.header {
    background: var(--primary);
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
    align-items: center;
    display: flex;
    flex-direction: column;
}

.header-background {
    /* position: absolute; */
    width: 100%;
    height: 105px;
    line-height: 105px;
    left: 0px;
    top: 0px;
    /* background: #16ABF8; */
    
}

.header-title {
    font-weight: 700;
    font-size: 24px;
    /* line-height: 36px; */
    /* identical to box height */
    text-transform: uppercase;
    color: #FFFFFF;
    display: inline-block;
    vertical-align: middle;
    margin: 0;
}
