.activity-item {
    min-height: 234px;
    background: #FFFFFF;
    box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.1);
    border-radius: 12px;
    padding: 22px 26px 25px 27px;
    display: flex;
    flex-direction: column;
}

.activity-item-footer {
    margin-top: auto;
}

.activity-item-footer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.activity-item-title {
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 27px;
    margin: 0;
}

.activity-item-dates {
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 21px;
    color: #888888;
    margin: 0;
}